<template>
    <div>
		<!--图片轮播-->
        <div class="am-slider am-slider-default" data-am-flexslider id="demo-slider-0">
		  	<ul class="am-slides">
		    	<li><img src="../assets/img/banner/discount-banner1.jpg" /></li>
		    	<li><img src="../assets/img/banner/discount-banner2.jpg" /></li>
		    	<li><img src="../assets/img/banner/discount-banner3.jpg" /></li>
		  	</ul>
		</div>
	    <div data-am-widget="tabs" class="am-tabs-d2">
		    <div class="am-tabs-bd">
		        <div class="am-tab-panel am-active">
		            <ul class="new" id="discount">
		             	<li v-for="(item,index) in ret.list" :key="index" >
		             		<a v-on:click="getDicountById(item.id)">
		             			<div class="text">
		             				<h3 class="discount_title">{{item.title}}</h3>
		             			</div>
		             		</a>
		             	</li>	             			             	
		            </ul>
		        </div>
		    </div>
        </div>
	</div>
</template>
<script>
export default {
    name : 'DiscountList' , 
    data(){
        return{
            YouHuiId:1,
            ret:[],
            list:[],
            host:'/apis/wxWebApp/upload'	//调取图片
        }
    } , 
    created : function (){
        //获取到列表
        this.getList();
    } ,
    methods :{
        //获取列表
        getList : function (){
            fetch("/apis/wxWebApp/youhui/getYouHuiListByPage.action",{	//调后台接口
                method:"POST",
                body : JSON.stringify({
                    currentPage : 1
                })
			})
			.then(res => res.json())
			.then(data => {
                this.ret = data ;
                //console.log("获取列表"+data);
            })
        } ,
        //根据优惠讯息ID获取优惠讯息
		getDicountById:function(id){
      		//console.log('传过来的ID'+id)
			this.$router.push({
				path : '/discount/' + id
			})
		}
    }
}
</script>



